
import { Link } from "react-router-dom";
import React from 'react'

export function ToyPreview({ toy }) {
    return (
        <Link to={`/toy/details/${toy._id}`}>
            <img src={require(`../assets/img/toys/toy${toy.img}.png`)} />
            <div className="preview-txt">
                <p >{toy.name} </p> |
                <p >${toy.price} </p>
            </div>
        </Link>
    )
}
